.dashboard {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: auto;
  text-align: center;
  --header-height: 80px;
}
.dashboard > *:not(.header) {
  min-height: calc(100% - var(--header-height));
  max-height: calc(100% - var(--header-height));
  width: 100%;
  box-sizing: border-box;
  flex: 1;
  overflow: auto;
}
.dashboard > *:not(.header):not(.kings-corner) {
  padding-bottom: 30px;
}
.dashboard > *.header {
  height: var(--header-height);
}/*# sourceMappingURL=dashboard.css.map */